import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "../../../../../constants/validate";
import { update } from "../store/action";

const DiscountTab = ({ selected }) => {
  const { register, errors, handleSubmit } = useForm();
  const [discountData, setDiscountData] = useState(null);
  const store = useSelector((state) => state.discounts);

  const DiscountOptions = validateOptions.DiscountOptions;
  const dispatch = useDispatch();
  const history = useHistory();
  const [disable, setDisable] = useState(false);
  const { id } = useParams();
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        discountData !== null &&
        selected.id !== discountData.id)
    ) {
      setDiscountData(selected);
    }
  }, [selected]);

  const onSubmit = (values) => {
    if (isObjEmpty(errors)) {
      setDisable(true);
      dispatch(
        update({
          product_id: parseInt(id),
          ...values,
        })
      );
    }
  };

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.username} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          {/* <FormGroup>
            <Label for="service">
              <FormattedMessage id="Service" />
            </Label>

            <Input
              type="select"
              name="service"
              id="service"
              defaultValue={discountData && discountData.status}
              innerRef={register({ required: true })}
            >
              <option value="local">Local</option>
              <option value="cloud">Cloud</option>
            </Input>
          </FormGroup> */}

          <div className="mt-1"></div>
          <FormGroup>
            <Label for="discount ">
              <FormattedMessage id="discount" />

              <span className="text-danger">*</span>
            </Label>
            <Input
              name="discount"
              id="discount"
              placeholder=" "
              innerRef={register(DiscountOptions.time_price)}
              className={classNames({ "is-invalid": errors["discount"] })}
              defaultValue={discountData && discountData.last_name}
              type="number"
            />
            <small className="text-danger">
              {errors?.time_price && errors.time_price.message}
            </small>
          </FormGroup>
          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onCLick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(DiscountTab);
